<template>
    <div class='hundred'>
        <div class='white'>
            <div class='myTitle'>
                <div class='allFlex headTitle'>
                    <div class='headUpload'>
                        <el-upload
                            class='upload-demo'
                            action='#'
                            ref='upload'
                            :show-file-list='false'
                            :multiple='true'
                            accept='image/*'
                            :http-request='none'
                            :on-change='handleChange'
                            :on-exceed='handleChangeMax'
                            :file-list='fileList'
                            :auto-upload='true'
                            :limit='5'
                            list-type='picture-card'>
                            <img :src="url?url:require('../../assets/head.png')" />
                        </el-upload>
                    </div>
                    <div class='nameWidth'>
                        <div class='myName'>
                            {{merchantCnName?merchantCnName:phone}}
                        </div>
                        <div>{{phone}}</div>
                    </div>
                    <div class='levelWidth'>
                        <span class='levelName'>{{level}}</span>
                    </div>
                </div>
            </div>
            <div class='myBorder myMargin'>
                <div class='allFlex'>
                    <router-link :to="{name: 'wallet'}" tag='div' class='halfSix allFlex'>
                        <div class='half'>
                            <p class='myMoney'>{{Number(sumSwitch)?'***':income}}</p>
                            <div class='moneyCaption'>总收益</div>
                        </div>
                        <div class='half'>
                            <p class='myMoney'>{{Number(sumSwitch)?'***':withdraw}}</p>
                            <div class='moneyCaption'>可提现收益</div>
                        </div>
                    </router-link>
                    <div class='halfFour'>
                        <img @click='show()' :src="Number(sumSwitch) ? require('../../assets/my/nolook.png') : require('../../assets/my/look.png')" class='look'/>
                        <router-link :to="{name: 'retreat'}" tag='span' class='levelName levelColor'>立即提现</router-link>
                    </div>
                </div>
            </div>
            <div class='allFlex'>
                <div @click='toReal()' class='half textCenter'>
                    <img src='../../assets/my/real.png'  class='imageTwo immargin' />
                    <p>实名认证</p>
                </div>
                <router-link :to="{name: 'repayment'}" tag='div' class='half textCenter'>
                    <img src='../../assets/my/card.png'  class='imageTwo immargin' />
                    <p>我的卡包</p>
                </router-link>
                <router-link :to="{name: 'team'}" tag='div' class='half textCenter'>
                    <img src='../../assets/my/team.png'  class='imageTwo immargin' />
                    <p>我的团队</p>
                </router-link>
                <router-link :to="{name: 'upgrade'}" tag='div' class='half textCenter'>
                    <img src='../../assets/my/up.png'  class='imageTwo immargin' />
                    <p>会员升级</p>
                </router-link>
            </div>
        </div>
        <div class='centerSelect'>
            <router-link :to="{name: 'wallet'}" tag='div' class='allFlex'>
                <img src='../../assets/my/wallet.png' class='myImage'/>
                我的钱包
                <img src='../../assets/right.png' class='rightNext'/>
            </router-link>
            <router-link :to="{name: 'alldetails'}" tag='div' class='allFlex'>
                <img src='../../assets/my/detail.png' class='myImage'/>
                交易明细
                <img src='../../assets/right.png' class='rightNext'/>
            </router-link>
            <router-link :to="{name: 'share'}" tag='div' class='allFlex'>
                <img src='../../assets/my/share.png' class='myImage'/>
                邀请分享
                <img src='../../assets/right.png' class='rightNext'/>
            </router-link>
            <router-link :to="{name: 'customer'}" tag='div' class='allFlex'>
                <img src='../../assets/my/phone.png' class='myImage'/>
                联系客服
                <img src='../../assets/right.png' class='rightNext'/>
            </router-link>
            <div class='allFlex'  @click="problem('http://ttc.chinalife-p.com.cn/MSF/wecheat-html/product/producd123.html?pid=123&productId=&salesMenNO=430602198704072515&sysId=17&productNo=&organizationId=4403983396&saleChannelId=03&businessNature=k&agentId=89230719-7&agencyAgree=89230719-7-01&disRate=no&handlerCode=44023131005902&lifeOperatorCode=430602198704072515&makecom=44039895&approvercode=430602198704072515&sysSourceCode=60&riskcode=0307&verId=2&saleInfoId=870055&identificationCode=null&extParams=&history=1', '我的保单')">
                <img src='../../assets/my/policy.png' class='myImage'/>
                我的保单
                <img src='../../assets/right.png' class='rightNext'/>
            </div>
            <div class='allFlex' @click="problem('https://q.eqxiu.com/s/Y4BRKjjU', '操作手册')">
                <img src='../../assets/my/book.png' class='myImage'/>
                操作手册
                <img src='../../assets/right.png' class='rightNext'/>
            </div>
            <div class='allFlex' @click="problem('http://yihuan.llyzf.cn/lly-posp-proxy/yh/helpCenter.html', '常见问题')">
                <img src='../../assets/my/problem.png' class='myImage'/>
                常见问题
                <img src='../../assets/right.png' class='rightNext'/>
            </div>
            <router-link :to="{name: 'setting'}" tag='div' class='allFlex'>
                <img src='../../assets/my/set.png' class='myImage'/>
                系统设置
                <img src='../../assets/right.png' class='rightNext'/>
            </router-link>
        </div>
        <div class='bottomLong' element-loading-background="rgba(0, 0, 0, 0.7)" v-loading.fullscreen.lock='fullscreenLoading'></div>
    </div>
</template>
<script>
import axios from 'axios'

export default {
  data () {
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      fileList: [],
      imgUrlBase64: {},
      url: '',
      phone: {},
      version: '',
      agentNo: '',
      level: '',
      list: [],
      merchantNo: '',
      parentPhone: '',
      merchantCnName: '',
      income: 0,
      withdraw: 0,
      sumSwitch: 0,
      fullscreenLoading: false,
      blem: [],
      real: 0,
      freezeStatus: '',
      levelx: {
        1: '会员',
        2: '银牌代理',
        3: '金牌代理',
        4: '区级代理',
        5: '市级代理',
        6: '省级代理',
        7: '运营合伙人'
      }
    }
  },
  created () {
    this.version = this.$stact.state.version
    this.agentNo = this.$stact.state.agentNo
    this.real = this.$stact.state.real
    this.merchantCnName = JSON.parse(this.$stact.state.token)[0].merchantCnName
    this.level = this.levelx[JSON.parse(this.$stact.state.token)[0].level]
    console.log(this.$stact.state.level)
    this.freezeStatus = JSON.parse(this.$stact.state.token)[0].freezeStatus
    this.phone = JSON.parse(this.$stact.state.token)[0].phone.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')
    this.merchantNo = JSON.parse(this.$stact.state.token)[0].merchantNo
    this.message()
  },
  methods: {
    async handleChange (file, fileList) {
      console.log(fileList)
      if (fileList.length > 0) {
        this.fullscreenLoading = true
        this.fileList = fileList
        this.url = fileList[fileList.length - 1].url
        console.log(fileList[fileList.length - 1])
        await this.chageBase(fileList[fileList.length - 1])
      }
    },
    async handleChangeMax (file, fileList) {
      vm.$message({
        message: '上传图片次数过多，请勿重复',
        center: true,
        offset: 30,
        duration: 2500,
        type: 'success'
      })
    },
    none () {
      console.log(1)
    },
    chageBase (fileList) {
      console.log(fileList)
      let vm = this
      let file = fileList.raw
      // 将文件以Data URL形式读入页面
      let reader = new FileReader()
      this.imgUrlBase64 = reader.readAsDataURL(file)
      reader.onload = function () {
        let imgBase = reader.result.split(',')[1]
        let parmas = {
          'imageData': imgBase,
          'imageType': '10T',
          'merchantNo': vm.merchantNo
        }
        let config = {
          headers: {
            'Content-Type': 'application/json; charset=utf-8'
          }
        }
        const root = process.env.NODE_ENV === 'production' ? 'http://yihuan.llyzf.cn/lly-posp-proxy/' : '/apis'
        axios.post(root + 'app/imageUpload.app', parmas, config)
          .then(res => {
            vm.fullscreenLoading = false
            this.fileList = []
            vm.$message({
              message: res.data.msg,
              center: true,
              offset: 30,
              duration: 2500,
              type: 'success'
            })
          })
          .catch(err => {
            console.log(err)
            vm.fullscreenLoading = false
          })
      }
    },
    problem (url, title) {
      this.$router.push({ name: 'ifarme', query: { url: url, title: title } })
      // let vm = this
      // let parmas = {
      //   '0': '0700',
      //   '3': '790013',
      //   '42': vm.merchantNo,
      //   '59': vm.version
      // }
      // let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      // vm.$http.get(`request.app${url}`)
      //   .then(res => {
      //     if (res.data[39] === '00') {
      //     }
      //   })
      //   .catch(err => {
      //     console.log(err)
      //   })
    },
    message () {
      let vm = this
      let parmas = {
        '0': '0700',
        '3': '190112',
        '42': vm.merchantNo,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            vm.$stact.dispatch('SetToken', res.data[57]) // 存token
            vm.$stact.dispatch('SetReal', res.data[60])
            vm.url = res.data[14]
            vm.sumSwitch = JSON.parse(vm.$stact.state.token)[0].sumSwitch
            vm.freezeStatus = JSON.parse(this.$stact.state.token)[0].freezeStatus
            vm.income = res.data[46]
            vm.withdraw = res.data[6]
            if (vm.freezeStatus === '10B' && document.querySelector('.v-modal') !== null) {
              document.querySelector('.v-modal').style.display = 'none'
              document.querySelector('.el-message-box__wrapper').style.display = 'none'
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    show () {
      let vm = this
      console.log(vm.sumSwitch)
      Number(vm.sumSwitch) === 1 ? vm.sumSwitch = 0 : vm.sumSwitch = 1
      let parmas = {
        '0': '0700',
        '3': '790009',
        '42': vm.merchantNo,
        '43': 'Z',
        '44': vm.sumSwitch,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            console.log(res)
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    toReal () {
      let vm = this
      if (vm.freezeStatus !== '10B') {
        vm.$router.push({ name: 'real' })
      } else {
        vm.$message({
          message: '已实名或者审核中',
          center: true,
          offset: 30,
          duration: 2500,
          type: 'success'
        })
      }
    }
  }
}
</script>
